<!-- src/components/ShowcaseSidebar.vue -->
<template>
  <div>
    <div class="overlay" v-if="isOpen" @click="$emit('close')"></div>
    <div class="showcase-sidebar" :class="{ 'is-open': isOpen }">
      <button class="close-button" @click="$emit('close')">&times;</button>
      <div v-if="item" class="showcase-content">
        <h2>{{ item.name }}</h2>
        <div v-if="item.description" class="item-description">
          <p>{{ item.description }}</p>
        </div>
        <div v-if="hasSamples" class="item-samples">
          <div v-if="category === 'music'" class="music-samples">
            <h3>Sample Tracks</h3>
            <div v-for="(sample, index) in musicSamples" :key="index" class="sample-item">
              <h4>Sample {{ index + 1 }}</h4>
              <div v-if="sample.type === 'youtube'" class="youtube-embed">
                <iframe 
                  width="100%" 
                  height="315" 
                  :src="'https://www.youtube.com/embed/' + sample.id" 
                  frameborder="0" 
                  allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" 
                  allowfullscreen
                ></iframe>
              </div>
              <div v-else-if="sample.type === 'audio'" class="audio-player">
                <audio controls>
                  <source :src="getAudioUrl(sample.url)" type="audio/mpeg">
                  Your browser does not support the audio element.
                </audio>
              </div>
            </div>
          </div>
          <div v-if="imageSamples.length > 0" class="artwork-samples">
            <h3>Artwork Samples</h3>
            <div class="sample-images">
              <img v-for="(sample, index) in imageSamples" :key="index" :src="getImageUrl(sample)" :alt="item.name + ' sample ' + (index + 1)" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { computed } from 'vue';

export default {
  name: 'ShowcaseSidebar',
  props: {
    item: {
      type: Object,
      required: true
    },
    category: {
      type: String,
      required: true
    },
    isOpen: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const hasSamples = computed(() => {
      return props.item.sample_1 || props.item.sample_2 || props.item.sample_3 ||
             props.item.url_sample_1 || props.item.url_sample_2 || props.item.url_sample_3;
    });

    const imageSamples = computed(() => {
      return [props.item.sample_1, props.item.sample_2, props.item.sample_3]
        .filter(sample => sample && !sample.endsWith('.mp3'));
    });

    const musicSamples = computed(() => {
      const samples = [];
      for (let i = 1; i <= 3; i++) {
        const urlSample = props.item[`url_sample_${i}`];
        const localSample = props.item[`sample_${i}`];
        
        if (urlSample && urlSample.includes('youtube.com')) {
          const videoId = urlSample.split('v=')[1];
          samples.push({ type: 'youtube', id: videoId });
        } else if (localSample && localSample.endsWith('.mp3')) {
          samples.push({ type: 'audio', url: localSample });
        }
      }
      return samples;
    });

    return {
      hasSamples,
      imageSamples,
      musicSamples
    };
  },
  methods: {
    getImageUrl(image) {
      return `${process.env.BASE_URL}assets/favorites/${image}`;
    },
    getAudioUrl(audio) {
      return `${process.env.BASE_URL}assets/favorites/${audio}`;
    }
  }
};
</script>

