  position: fixed;
  top: 0;
  right: -300px;
  width: 300px;
  height: 100%;
  background-color: var(--background-color-secondary);
  transition: right 0.3s ease;
  overflow-y: auto;
  z-index: 1000;
}

.showcase-sidebar.is-open {
  right: 0;
}




.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
}

.showcase-content {
  padding: 20px;
}

.item-description {
  margin-bottom: var(--spacing-medium);
}

.audio-player,
.artwork-samples {
  margin-top: var(--spacing-medium);
}

.sample-images {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-small);
}

.sample-images img {
  max-width: 100%;
  height: auto;
}

.music-samples {
  margin-top: var(--spacing-medium);
}

.sample-item {
  margin-bottom: var(--spacing-medium);
}

.youtube-embed {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 aspect ratio */
  height: 0;
