<template>
  <div class="item-card" :class="currentView" @click="handleClick">
    <template v-if="currentView === 'compact'">
      <div class="item-content">
        <p class="item-title">{{ truncateText(item.name, 40) }}</p>
        <p v-if="item.description" class="item-subtitle">{{ truncateText(item.description, 60) }}</p>
      </div>
    </template>
    
    <template v-else-if="currentView === 'list'">
      <div class="item-image-container">
        <img v-if="item.image" :src="getImageUrl(item.image)" :alt="item.name + ' image'" class="item-image">
        <div v-else class="item-image-placeholder">
          <font-awesome-icon :icon="getCategoryIcon(category)" />
        </div>
      </div>
      <div class="item-details">
        <div class="item-title-container">
          <span class="item-title">{{ item.name }}</span>
        </div>
        <div v-if="item.description" class="item-subtitle">{{ truncateText(item.description, 100) }}</div>
      </div>
    </template>

    <template v-else-if="currentView === 'grid'">
      <div class="grid-item-container">
        <img v-if="item.image" :src="getImageUrl(item.image)" :alt="item.name + ' image'" class="grid-item-image">
        <div v-else class="grid-item-image-placeholder">
          <font-awesome-icon :icon="getCategoryIcon(category)" />
        </div>
        <p class="grid-item-title">
          {{ truncateText(item.name, 30) }}
        </p>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  name: 'FavoriteItem',
  props: {
    item: {
      type: Object,
      required: true
    },
    category: {
      type: String,
      required: true
    },
    currentView: {
      type: String,
      default: 'list'
    }
  },
  methods: {
    getImageUrl(image) {
      return `${process.env.BASE_URL}assets/favorites/${image}`;
    },
    getCategoryIcon(category) {
const iconMap = {
        artists: 'palette',
        'artists (digital)': 'palette',
        movies: 'film',
        books: 'book',
        youtubers: ['fab', 'youtube'],
        music: 'music',
        philosophers: 'brain',
        scientists: 'flask',
        tvShows: 'tv',
        podcasts: 'podcast',
        videoGames: 'gamepad',
        anime: 'dragon',
        cuisines: 'utensils',
        places: 'globe',
        programmingLanguages: 'code',
        artMovements: 'paint-brush',
        historicalFigures: 'landmark',
        inventions: 'lightbulb',
        hobbies: 'puzzle-piece',
        charities: 'hand-holding-heart',
        quotes: 'quote-left',
        blogs: 'rss',
        comics: 'book-open',
        fashionStyles: 'tshirt',
        mathematicalConcepts: 'square-root-alt',
        scientificTheories: 'atom',
        philosophicalConcepts: 'yin-yang',
        blockchainProjects: 'cubes'
      };
      return iconMap[category] || 'star';
    },
    truncateText(text, maxLength) {
      if (!text) return '';
      return text.length <= maxLength ? text : text.slice(0, maxLength - 3) + '...';
    },
    handleClick() {
      this.$emit('toggleShowcase', this.item, this.category);
    }
  }
};
</script>





