<!-- src/views/About.vue -->
<template>
  <div class="about content-container">
    <div class="tabs-container">
      <button 
        v-for="tab in tabs" 
        :key="tab.key"
        @click="currentTab = tab.key"
        :class="['tab-button', { active: currentTab === tab.key }]"
      >
        {{ tab.label }}
      </button>
    </div>

    <!-- Overview Tab -->
    <div v-if="currentTab === 'overview'" class="overview">
      <div v-if="isLoading" class="loading">Loading overview...</div>
      <div v-else-if="error" class="error">{{ error }}</div>
      <template v-else>
        <p v-for="(paragraph, index) in overviewParagraphs" :key="index">{{ paragraph }}</p>
      </template>
    </div>

    <!-- Favorites Tab -->
    <div v-else-if="currentTab === 'favorites'" class="favorites">
      <div class="controls">
        <ViewSelector v-model="currentView" />
      </div>

      <div v-if="!favoritesExist" class="no-favorites">
        <p>No favorites found. Please add some favorites to see them here.</p>
      </div>
      <template v-else>
        <div v-for="(items, category) in favorites" :key="category" class="favorite-category">
          <h2>{{ formatCategory(category) }}</h2>
          <div v-if="items && items.length > 0" :class="['favorite-list', viewClass]">
            <FavoriteItem
              v-for="item in items"
              :key="item.name"
              :item="item"
              :category="category"
              :currentView="currentView"
              @toggleShowcase="toggleShowcase"
            />
          </div>
          <p v-else>No items found in this category.</p>
        </div>
      </template>
    </div>

    <ShowcaseSidebar
      v-if="showcaseItem"
      :item="showcaseItem"
      :category="showcaseCategory"
      :isOpen="showShowcase"
      @close="closeShowcase"
    />
  </div>
</template>

<script>
import { ref, computed, onMounted, watch } from 'vue'
import { useStore } from 'vuex'
import { useViewSelector } from '@/composables/useViewSelector'
import ViewSelector from '@/components/ViewSelector.vue'
import FavoriteItem from '@/components/FavoriteItem.vue'
import ShowcaseSidebar from '@/components/ShowcaseSidebar.vue'

export default {
  name: 'About',
  components: { ViewSelector, FavoriteItem, ShowcaseSidebar },
  setup() {
    const store = useStore()
    const currentTab = ref('overview')
    const { currentView, viewClass } = useViewSelector('grid')

    const tabs = [
      { key: 'overview', label: 'Overview' },
      { key: 'favorites', label: 'Favorites' }
    ]

    const overview = ref('')
    const favorites = ref({})
    const isLoading = ref(true)
    const error = ref(null)

    const showcaseItem = ref(null)
    const showcaseCategory = ref('')
    const showShowcase = ref(false)

    const loadContent = async () => {
      try {
        await store.dispatch('content/loadOverview')
        await store.dispatch('favorites/loadFavorites')
        overview.value = store.getters['content/getOverview']
        favorites.value = store.getters['favorites/getFavorites']
        isLoading.value = false
      } catch (err) {
        console.error('Failed to load content:', err)
        error.value = 'Failed to load content. Please try again later.'
        isLoading.value = false
      }
    }

    onMounted(loadContent)

    const overviewParagraphs = computed(() => overview.value ? overview.value.split('\n\n') : [])

    const favoritesExist = computed(() => {
      return Object.keys(favorites.value).length > 0 && 
             Object.values(favorites.value).some(category => category && category.length > 0)
    })

    const formatCategory = (category) => {
      return category.split(/(?=[A-Z])/).join(' ')
    }

    const toggleShowcase = (item, category) => {
      if (showcaseItem.value === item) {
        closeShowcase()
      } else {
        showcaseItem.value = item
        showcaseCategory.value = category
        showShowcase.value = true
      }
    }

    const closeShowcase = () => {
      showShowcase.value = false
      setTimeout(() => {
        showcaseItem.value = null
        showcaseCategory.value = ''
      }, 300)
    }

    // Debug: Log when overview is loaded
    watch(overview, (newOverview) => {
      console.log('Overview loaded:', newOverview)
    })

    // Debug: Log when favorites are loaded
    watch(favorites, (newFavorites) => {
      console.log('Favorites loaded:', newFavorites)
    })

    return {
      currentTab,
      currentView,
      viewClass,
      tabs,
      overviewParagraphs,
      favorites,
      favoritesExist,
      formatCategory,
      showcaseItem,
      showcaseCategory,
      showShowcase,
      toggleShowcase,
      closeShowcase,
      isLoading,
      error
    }
  }
}
</script>

<style scoped>
@import '../assets/css/view-styles.css';
@import '../assets/css/tab-view-styles.css';

.about {
  padding: var(--spacing-medium);
}

.overview p {
  margin-bottom: var(--spacing-medium);
}

.loading, .error, .no-favorites {
  text-align: center;
  padding: var(--spacing-large);
  font-size: var(--font-size-large);
  color: var(--text-color-secondary);
}

.error {
  color: var(--error-color, #ff0000);
}

h2 {
  margin-top: var(--spacing-large);
  margin-bottom: var(--spacing-medium);
  color: var(--highlight-color);
}

.favorite-list + h2 {
  margin-top: var(--spacing-large);
}
</style>
