<!-- src/views/Physical.vue -->
<template>
  <div class="physical-activities content-container">
    <div class="tabs-container">
      <button 
        v-for="tab in tabs" 
        :key="tab.key"
        @click="currentTab = tab.key"
        :class="['tab-button', { active: currentTab === tab.key }]"
      >
        {{ tab.label }}
      </button>
    </div>

    <div class="controls">
      <div class="sort-options">
        <span>Sort:</span>
        <button 
          v-for="option in sortOptions" 
          :key="option.value"
          @click="currentSort = option.value"
          :class="{ active: currentSort === option.value }"
        >
          <i :class="['fas', option.icon]"></i> {{ option.label }}
        </button>
      </div>
      <ViewSelector v-model="currentView" />
    </div>

    <div v-if="isLoading" class="loading">Loading activities...</div>
    <div v-else-if="error" class="error">{{ error }}</div>
    <div v-else-if="filteredActivities.length === 0" class="no-activities">
      No activities found in the "{{ currentTab }}" category.
    </div>
    <div v-else :class="['activity-list', viewClass]">
      <ActivityItem
        v-for="activity in filteredActivities"
        :key="activity.id"
        :activity="activity"
        :currentView="currentView"
      />
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue'
import { useStore } from 'vuex'
import { useViewSelector } from '@/composables/useViewSelector'
import ViewSelector from '@/components/ViewSelector.vue'
import ActivityItem from '@/components/ActivityItem.vue'

export default {
  name: 'PhysicalActivities',
  components: { ViewSelector, ActivityItem },
  setup() {
    const store = useStore()
    const { currentView, viewClass } = useViewSelector('list')

    const currentTab = ref('current')
    const currentSort = ref('name')
    const isLoading = ref(true)
    const error = ref(null)

    const tabs = [
      { key: 'current', label: 'Current' },
      { key: 'achieved', label: 'Achieved' },
      { key: 'planned', label: 'Planned' }
    ]

    const sortOptions = [
      { value: 'name', label: 'Name', icon: 'fa-sort-alpha-down' },
      { value: 'progress', label: 'Progress', icon: 'fa-tasks' },
      { value: 'category', label: 'Category', icon: 'fa-layer-group' }
    ]

    const loadActivities = async () => {
      try {
        await store.dispatch('activities/loadActivities')
        isLoading.value = false
      } catch (err) {
        console.error('Failed to load activities:', err)
        error.value = 'Failed to load activities. Please try again later.'
        isLoading.value = false
      }
    }

    onMounted(loadActivities)

    const activities = computed(() => store.getters['activities/getActivities'])

    const filteredActivities = computed(() => {
      return activities.value.filter(activity => activity.status === currentTab.value)
        .sort((a, b) => {
          switch (currentSort.value) {
            case 'name':
              return a.name.localeCompare(b.name)
            case 'progress':
              return (b.progress || 0) - (a.progress || 0)
            case 'category':
              return a.category.localeCompare(b.category)
            default:
              return 0
          }
        })
    })

    return {
      currentTab,
      currentView,
      viewClass,
      currentSort,
      tabs,
      sortOptions,
      filteredActivities,
      isLoading,
      error
    }
  }
}
</script>

<style scoped>
@import '../assets/css/view-styles.css';
@import '../assets/css/tab-view-styles.css';

.physical-activities {
  padding: var(--spacing-medium);
}

.loading, .error, .no-activities {
  text-align: center;
  padding: var(--spacing-large);
  font-size: var(--font-size-large);
  color: var(--text-color-secondary);
}

.error {
  color: var(--error-color, #ff0000);
}
</style>
