<!-- src/components/Sidebar.vue -->
<template>
  <div class="sidebar-container" :class="{ 'sidebar-open': isOpen }">
    <div class="sidebar">
      <nav v-if="!showingToc">
        <ul>
          <li v-for="item in menuItems" :key="item.path">
            <router-link :to="item.path" exact-active-class="active" @click="handleItemClick">
              <font-awesome-icon :icon="item.icon" class="nav-icon" />
              {{ item.name }}
            </router-link>
          </li>
        </ul>
      </nav>
      <div v-else-if="tableOfContents.length > 0" class="toc-content">
        <button @click="showingToc = false" class="back-button">
          <font-awesome-icon :icon="['fas', 'chevron-left']" />
          <span>Back</span>
        </button>
        <h3>Table of Contents</h3>
        <ul class="toc-list">
          <li v-for="item in tableOfContents" :key="item.id" :class="{ 'toc-h1': item.level === 1, 'toc-h2': item.level === 2, 'toc-h3': item.level === 3 }">
            <a :href="`#${item.id}`" @click.prevent="handleTocClick(item.id)">{{ item.text }}</a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, ref, watch } from 'vue'
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'

export default {
  name: 'SidebarMenu',
  setup() {
    const store = useStore()
    const route = useRoute()

    const isOpen = computed(() => store.state.content.sidebarOpen)
    const showingToc = ref(false)
    const tableOfContents = computed(() => store.state.content.tableOfContents)

    const handleItemClick = () => {
      if (window.innerWidth <= 768) {
        store.dispatch('content/setSidebarOpen', false)
      }
    }

    const handleTocClick = (id) => {
      scrollToSection(id)
      if (window.innerWidth <= 768) {
        store.dispatch('content/setSidebarOpen', false)
      }
    }

    const scrollToSection = (id) => {
      const element = document.getElementById(id)
      if (element) {
        const topBarHeight = 60 // Adjust this value to match your top bar height
        const offset = 20 // Additional offset for visual comfort
        const elementPosition = element.getBoundingClientRect().top
        const offsetPosition = elementPosition + window.pageYOffset - topBarHeight - offset

        window.scrollTo({
          top: offsetPosition,
          behavior: 'smooth'
        })
      }
    }

    const menuItems = [
      { name: 'About', path: '/about', icon: 'user-circle' },
      { name: 'Books', path: '/books', icon: 'book' },
      { name: 'Writing', path: '/blog', icon: 'pen' },
      { name: 'Physical', path: '/activities', icon: 'dumbbell' },
      { name: 'Contact', path: '/contact', icon: 'envelope' }
    ]

    watch(() => route.path, () => {
      showingToc.value = false
    })

    watch(tableOfContents, (newToc) => {
      if (newToc.length > 0) {
        showingToc.value = true
      }
    })

    return {
      isOpen,
      showingToc,
      handleItemClick,
      handleTocClick,
      menuItems,
      tableOfContents
    }
  }
}
</script>

