<!-- src/views/Contact.vue -->
<template>
  <div class="contact content-container">
    
    <div class="tabs-container">
      <button 
        v-for="tab in tabs" 
        :key="tab.key"
        @click="currentTab = tab.key"
        :class="['tab-button', { active: currentTab === tab.key }]"
      >
        {{ tab.label }}
      </button>
    </div>

    <div class="controls">
      <ViewSelector v-model="currentView" :options="viewOptions" />
    </div>

    <div :class="['contact-list', viewClass]">
      <ContactItem
        v-for="option in currentContacts"
        :key="option.name"
        :contact="option"
        :currentView="currentView"
        @copy="copyToClipboard"
      />
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue'
import { useStore } from 'vuex'
import { useViewSelector } from '@/composables/useViewSelector'
import ViewSelector from '@/components/ViewSelector.vue'
import ContactItem from '@/components/ContactItem.vue'

export default {
  name: 'Contact',
  components: { ViewSelector, ContactItem },
  setup() {
    const store = useStore()
    const currentTab = ref('communication')
    const { currentView, viewClass } = useViewSelector('compact')

    const tabs = [
      { key: 'communication', label: 'Professional' },
      { key: 'messaging', label: 'Messaging' },
      { key: 'payments', label: 'Payments' }
    ]

    const viewOptions = [
      { value: 'compact', icon: 'fa-bars' },
      { value: 'grid', icon: 'fa-th-large' }
    ]

    onMounted(() => {
      store.dispatch('contact/loadContacts')
    })

    const currentContacts = computed(() => {
      const contacts = store.getters['contact/getContacts']
      return contacts[currentTab.value] || []
    })

    const copyToClipboard = (text) => {
      if (navigator.clipboard && window.isSecureContext) {
        navigator.clipboard.writeText(text).then(() => {
          alert('Copied to clipboard!')
        }).catch(err => {
          console.error('Failed to copy: ', err)
        })
      } else {
        // Fallback for older browsers or non-secure contexts
        const textArea = document.createElement("textarea")
        textArea.value = text
        document.body.appendChild(textArea)
        textArea.focus()
        textArea.select()
        try {
          document.execCommand('copy')
          alert('Copied to clipboard!')
        } catch (err) {
          console.error('Failed to copy: ', err)
        }
        document.body.removeChild(textArea)
      }
    }

    return {
      currentTab,
      currentView,
      viewClass,
      tabs,
      viewOptions,
      currentContacts,
      copyToClipboard
    }
  }
}
</script>

<style scoped>
@import '../assets/css/view-styles.css';
@import '../assets/css/tab-view-styles.css';

.contact {
  padding: var(--spacing-medium);
}

.page-title {
  margin-bottom: var(--spacing-large);
}

.contact-list {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-medium);
}

@media (max-width: 768px) {
  .contact-list {
    gap: var(--spacing-small);
  }
}
</style>
