// src/components/ViewSelector.vue
<template>
  <div class="view-toggle">
    <span>View:</span>
    <button 
      v-for="view in viewOptions" 
      :key="view.value"
      @click="changeView(view.value)"
      :class="{ active: modelValue === view.value }"
    >
      <i :class="['fas', view.icon]"></i>
    </button>
  </div>
</template>

<script>
import { useViewSelector } from '@/composables/useViewSelector'

export default {
  name: 'ViewSelector',
  props: {
    modelValue: {
      type: String,
      required: true
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    const { viewOptions } = useViewSelector()

    const changeView = (value) => {
      emit('update:modelValue', value)
    }

    return {
      viewOptions,
      changeView
    }
  }
}
</script>
