<!-- src/components/ContactItem.vue -->
<template>
  <div class="item-card" :class="currentView">
    <template v-if="currentView === 'compact'">
      <div class="item-content">
        <img :src="getIconUrl(contact.icon)" :alt="contact.name + ' icon'" class="contact-icon">
        <span class="item-title">{{ contact.title }}</span>
        <img :src="getImageUrl(isMobile ? contact['mobile-text'] : contact['desktop-text'])" :alt="contact.name + ' contact'" class="contact-text">
        <i v-if="contact.copyable" class="fas fa-copy copy-icon" @click="$emit('copy', contact.value)"></i>
      </div>
    </template>
    
    <template v-else>
      <div class="grid-item-container">
        <h3>{{ contact.title }}</h3>
        <img :src="getImageUrl(contact.qrCode)" :alt="contact.name + ' QR Code'" class="qr-code-image">
      </div>
    </template>
  </div>
</template>

<script>
import { ref, onMounted, onUnmounted } from 'vue'

export default {
  name: 'ContactItem',
  props: {
    contact: {
      type: Object,
      required: true
    },
    currentView: {
      type: String,
      default: 'compact'
    }
  },
  emits: ['copy'],
  setup() {
    const isMobile = ref(window.innerWidth <= 768)

    const checkMobile = () => {
      isMobile.value = window.innerWidth <= 768
    }

    onMounted(() => {
      window.addEventListener('resize', checkMobile)
    })

    onUnmounted(() => {
      window.removeEventListener('resize', checkMobile)
    })

    return {
      isMobile
    }
  },
  methods: {
    getIconUrl(icon) {
      return `${process.env.BASE_URL}assets/qr_icons/${icon}`
    },
    getImageUrl(image) {
      return `${process.env.BASE_URL}assets/contact/${image}`
    }
  }
}
</script>

<style scoped>
@import '../assets/css/view-styles.css';

.contact-icon {
  width: 24px;
  height: 24px;
  margin-right: var(--spacing-small);
}

.contact-text {
  max-width: calc(100% - 150px);
  height: auto;
  margin-left: auto;
}

.copy-icon {
  cursor: pointer;
  color: var(--highlight-color);
  margin-left: var(--spacing-small);
}

.qr-code-image {
  max-width: 200px;
  height: auto;
  margin: var(--spacing-small) auto;
}

.grid-item-container {
  text-align: center;
}

@media (max-width: 768px) {
  .compact-view .item-content {
    flex-wrap: wrap;
  }
  
  .compact-view .contact-text {
    max-width: calc(100% - 60px);
    margin-top: var(--spacing-small);
  }
}
</style>
