import contactData from '@/assets/contact.json'

export default {
  namespaced: true,
  state: () => ({
    contacts: {}
  }),
  mutations: {
    SET_CONTACTS(state, contacts) {
      state.contacts = contacts
    }
  },
  actions: {
    loadContacts({ commit }) {
      commit('SET_CONTACTS', contactData)
    }
  },
  getters: {
    getContacts: state => state.contacts
  }
}
