// src/composables/useViewSelector.js
import { ref, computed } from 'vue'

export function useViewSelector(defaultView = 'compact') {
  const currentView = ref(defaultView)

  const viewOptions = [
    { value: 'compact', icon: 'fa-bars' },
    { value: 'list', icon: 'fa-list' },
    { value: 'grid', icon: 'fa-th-large' }
  ]

  const changeView = (newView) => {
    currentView.value = newView
  }

  const viewClass = computed(() => `${currentView.value}-view`)

  return {
    currentView,
    viewOptions,
    changeView,
    viewClass
  }
}
