<!-- src/views/Books.vue -->
<template>
  <div class="books content-container">
    <div class="tabs-container">
      <button 
        v-for="tab in tabs" 
        :key="tab.key"
        @click="currentTab = tab.key"
        :class="['tab-button', { active: currentTab === tab.key }]"
      >
        {{ tab.label }}
      </button>
    </div>

    <div class="controls">
      <div class="sort-options">
        <span>Sort:</span>
        <button 
          v-for="option in sortOptions" 
          :key="option.value"
          @click="currentSort = option.value"
          :class="{ active: currentSort === option.value }"
        >
          <i :class="['fas', option.icon]"></i> {{ option.label }}
        </button>
      </div>
      <ViewSelector v-model="currentView" />
    </div>

    <div v-if="isLoading" class="loading">Loading books...</div>
    <div v-else-if="error" class="error">{{ error }}</div>
    <div v-else-if="filteredBooks.length === 0" class="no-books">
      No books found in the "{{ currentTab }}" category.
      Total books: {{ totalBooks }}, Filtered books: {{ filteredBooks.length }}
    </div>
    <div v-else :class="['book-list', viewClass]">
      <BookItem
        v-for="book in filteredBooks"
        :key="book.isbn || book.title"
        :book="book"
        :currentView="currentView"
      />
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, watch } from 'vue'
import { useStore } from 'vuex'
import { useViewSelector } from '@/composables/useViewSelector'
import ViewSelector from '@/components/ViewSelector.vue'
import BookItem from '@/components/BookItem.vue'

export default {
  name: 'Books',
  components: { ViewSelector, BookItem },
  setup() {
    const store = useStore()
    const { currentView, viewClass } = useViewSelector('grid')

    const currentTab = ref('reading')
    const currentSort = ref('title')
    const isLoading = ref(true)
    const error = ref(null)
    const tabs = [
      { key: 'reading', label: 'Currently Reading' },
      { key: 'read', label: 'Read' },
      { key: 'to-read', label: 'Want to Read' }
    ]

    const sortOptions = [
      { value: 'title', label: 'Title', icon: 'fa-sort-alpha-down' },
      { value: 'author', label: 'Author', icon: 'fa-user' },
      { value: 'dateRead', label: 'Date Read', icon: 'fa-calendar-alt' }
    ]

    const loadBooks = async () => {
      console.log('Loading books...')
      try {
        await store.dispatch('content/loadBooks')
        console.log('Books loaded successfully')
        isLoading.value = false
      } catch (err) {
        console.error('Failed to load books:', err)
        error.value = 'Failed to load books. Please try again later.'
        isLoading.value = false
      }
    }

    onMounted(() => {
      console.log('Books component mounted')
      loadBooks()
    })

    const books = computed(() => {
      const allBooks = store.getters['content/getBooks']
      console.log('All books:', allBooks)
      return allBooks
    })

    const totalBooks = computed(() => books.value.length)

    const filteredBooks = computed(() => {
      if (!books.value) return []
      return books.value.filter(book => {
        let result = false;
        switch (currentTab.value) {
          case 'reading':
            result = book.shelf === 'currently-reading' || book.tags.includes('currently-reading');
            break;
          case 'read':
            result = book.shelf === 'read';
            break;
          case 'to-read':
            result = book.shelf === 'to-read' || book.tags.includes('to-read') || book.tags.includes('wtr');
            break;
          default:
            result = true;
        }
        console.log(`Book: ${book.title}, Shelf: ${book.shelf}, Tags: ${book.tags}, Included: ${result}`);
        return result;
      }).sort((a, b) => {
        switch (currentSort.value) {
          case 'title':
            return a.title.localeCompare(b.title)
          case 'author':
            return a.author.localeCompare(b.author)
          case 'dateRead':
            return new Date(b.dateRead || 0) - new Date(a.dateRead || 0)
          default:
            return 0
        }
      })
    })

    watch(currentTab, (newTab) => {
      console.log('Current tab changed:', newTab)
    })

    watch(currentSort, (newSort) => {
      console.log('Current sort changed:', newSort)
    })

    watch(filteredBooks, (newFilteredBooks) => {
      console.log('Filtered books:', newFilteredBooks.length)
    })

    return {
      currentTab,
      currentView,
      viewClass,
      currentSort,
      tabs,
      sortOptions,
      filteredBooks,
      isLoading,
      error,
      totalBooks
    }
  }
}
</script>
