<!-- src/components/BlogItem.vue -->
<template>
  <div v-if="post" class="item-card" :class="currentView">
    <template v-if="currentView === 'compact'">
      <div class="item-content">
        <p class="item-title">{{ truncateTitle(post.title) }}</p>
        <p class="item-subtitle">{{ formatDate(post.date) }}</p>
      </div>
    </template>
    
    <template v-else>
      <div class="item-image-container">
        <img v-if="post.image" :src="post.image" :alt="post.title + ' cover'" class="item-image">
        <div v-else class="item-image-placeholder">
          <font-awesome-icon :icon="['fas', 'pen']" />
        </div>
      </div>
      <div class="item-details">
        <h3 class="item-title">{{ post.title }}</h3>
        <div class="item-subtitle">{{ formatDate(post.date) }}</div>
        <p v-if="currentView === 'list' && post.excerpt" class="item-excerpt">{{ post.excerpt }}</p>
        <div v-if="post.progress !== undefined" class="progress-container">
          <div class="progress-bar">
            <div class="progress" :style="progressStyle"></div>
          </div>
          <span class="progress-text">{{ post.progress }}%</span>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { computed } from 'vue'

export default {
  name: 'BlogItem',
  props: {
    post: {
      type: Object,
      required: true
    },
    currentView: {
      type: String,
      default: 'compact'
    }
  },
  setup(props) {
    const progressStyle = computed(() => ({
      width: `${props.post && props.post.progress !== undefined ? props.post.progress : 0}%`
    }))

    const formatDate = (dateString) => {
      if (!dateString) return 'Not available'
      const options = { year: 'numeric', month: 'long', day: 'numeric' }
      return new Date(dateString).toLocaleDateString(undefined, options)
    }

    const truncateTitle = (title) => {
      if (!title) return ''
      return title.length > 40 ? title.substring(0, 37) + '...' : title
    }

    return {
      progressStyle,
      formatDate,
      truncateTitle
    }
  }
}
</script>
<style scoped>
@import '../assets/css/tab-view-styles.css';

.item-image-container {
  position: relative;
  overflow: hidden;
}

.item-image-container::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 0px solid black;
  border-radius: 0px 20px 20px 0px;
  pointer-events: none;
}

.item-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 0px 20px 20px 0px;
}

.item-image-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--highlight-color);
  color: white;
  font-size: 2rem;
}

.item-excerpt {
  font-size: var(--font-size-small);
  margin-top: var(--spacing-small);
}
</style>
