<!-- src/components/MainContent.vue -->
<template>
  <main class="main-content" :class="{ 'sidebar-open': isSidebarOpen }">
    <div class="content-container">
      <router-view @toc-generated="updateTableOfContents"></router-view>
    </div>
  </main>
</template>

<script>
import { computed } from 'vue'
import { useStore } from 'vuex'

export default {
  name: 'MainContent',
  setup() {
    const store = useStore()
    const isSidebarOpen = computed(() => store.state.content.sidebarOpen)

    const updateTableOfContents = (toc) => {
      store.commit('content/setTableOfContents', toc)
    }

    return {
      isSidebarOpen,
      updateTableOfContents
    }
  }
}
</script>

<style scoped>
.main-content {
  padding: var(--spacing-medium);
  margin-top: 60px;
  margin-left: 0;
  transition: margin-left var(--transition-speed);
}

.content-container {
  max-width: var(--max-content-width);
  margin: 0 auto;
}

@media (min-width: 769px) {
  .main-content.sidebar-open {
    margin-left: 250px;
  }
}

@media (max-width: 768px) {
  .main-content.sidebar-open {
    margin-left: 0;
  }
}
</style>
