<template>
  <div class="blog-post-container" v-if="post">

    <div class="detail-level-container">
      <div class="detail-level-slider">
      <label for="detail-level">Detail Level slider:</label>
        <input 
          type="range" 
          id="detail-level" 
          v-model="currentDetailLevel" 
          :min="1" 
          :max="detailLevels.length" 
          step="1"
        >
      </div>
    </div>

    <div v-if="post.progress > 0" class="progress-container"> Writing Progress:
      <div class="progress-bar">
        <div class="progress" :style="{ width: `${post.progress}%` }"></div>
      </div>
      <span class="progress-text">
{{ post.progress }}%</span>
    </div>
    <div class="post-meta">
<span class="post-date"> Date: {{ formatDate(post.date) }}</span> <br>
<span class="read-time"> Reading Time: {{ readTime }} min </span>
    </div>

    <div ref="contentRef" v-html="processedContent" class="justified-content"></div>
  </div>
</template>

<script>
import { computed, onMounted, ref, watch, nextTick } from 'vue'
import MarkdownIt from 'markdown-it'
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'

export default {
  name: 'BlogPost',
  setup() {
    const store = useStore()
    const route = useRoute()
    const post = ref(null)
    const contentRef = ref(null)
    const readTime = ref(0)
    const currentDetailLevel = ref(1)
    const detailLevels = ['Basic', 'Intermediate', 'Full']

    const md = new MarkdownIt({
      html: true,
      linkify: true,
      typographer: true,
    })

    const loadPost = async () => {
      await store.dispatch('content/loadBlogPosts')
      post.value = store.getters['content/getBlogPost'](route.params.slug)
      if (post.value) {
        await loadPostContent()
        store.dispatch('content/setCurrentBlogPostTitle', post.value.title)
      }
    }

    const loadPostContent = async () => {
      try {
        const contentLevels = await Promise.all([
          import(`@/assets/blog/${post.value.slug}/${post.value.slug}_1.md`),
          import(`@/assets/blog/${post.value.slug}/${post.value.slug}_2.md`),
          import(`@/assets/blog/${post.value.slug}/${post.value.slug}.md`)
        ])
        post.value.contentLevels = contentLevels.map(content => content.default)
        processContent()
      } catch (error) {
        console.error('Error loading blog post content:', error)
      }
    }

    const processContent = () => {
      if (post.value && post.value.contentLevels) {
        const content = post.value.contentLevels[currentDetailLevel.value - 1]
        let htmlContent = md.render(content)
        
        // Extract headers and create table of contents
        const tempDiv = document.createElement('div')
        tempDiv.innerHTML = htmlContent
        const headers = tempDiv.querySelectorAll('h1, h2, h3')
        const tableOfContents = Array.from(headers).map((header, index) => {
          const id = `section-${index}`
          header.id = id
          return {
            id,
            text: header.textContent,
            level: parseInt(header.tagName[1])
          }
        })

        // Update the actual content with the new IDs
        headers.forEach((header, index) => {
          header.id = `section-${index}`
        })
        htmlContent = tempDiv.innerHTML

        // Dispatch the table of contents to the store
        store.dispatch('content/setTableOfContents', tableOfContents)

        // Replace image src with the correct path and add onerror handler
        htmlContent = htmlContent.replace(
          /<img src="(.*?)"/g,
          (match, p1) => `<img src="${process.env.BASE_URL}assets/blog/${post.value.slug}/${p1}" onerror="this.onerror=null;this.src='${process.env.BASE_URL}assets/placeholder-image.png';"`
        )

        post.value.processedContent = htmlContent

        // Calculate read time
        const words = content.trim().split(/\s+/).length
        readTime.value = Math.ceil(words / 200) // Assuming 200 words per minute reading speed

        // Render MathJax after content is updated
        nextTick(() => {
          renderMathJax()
        })
      }
    }

    const renderMathJax = () => {
      if (window.MathJax && contentRef.value) {
        window.MathJax.Hub.Queue(["Typeset", window.MathJax.Hub, contentRef.value])
      }
    }

    onMounted(loadPost)

    const processedContent = computed(() => post.value ? post.value.processedContent : '')

    const formatDate = (dateString) => {
      if (!dateString) return 'Not available'
      const options = { year: 'numeric', month: 'long', day: 'numeric' }
      return new Date(dateString).toLocaleDateString(undefined, options)
    }

    watch(processedContent, () => {
      nextTick(() => {
        renderMathJax()
      })
    })

    watch(currentDetailLevel, processContent)

    return {
      post,
      processedContent,
      formatDate,
      contentRef,
      readTime,
      currentDetailLevel,
      detailLevels
    }
  }
}
</script>

<style scoped>
.blog-post-container {
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
}

.post-meta {
  font-size: 14px;
  color: #666;
  margin-bottom: 20px;
}

.progress-container, .detail-level-container {
  margin-bottom: 20px;
}

.progress-bar, .detail-level-slider {
  width: 100%;
  height: 4px;
  background-color: #eee;
  border-radius: 5px;
  overflow: hidden;
}

.progress {
  height: 100%;
  background-color: #4a4a4a;
}

.progress-text, .detail-level-text {
  font-size: 14px;
  margin-top: 5px;
}

.detail-level-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.detail-level-slider {
  display: flex;
  align-items: center;
  width: 100%;
  height: 20px;
  background-color: transparent;
  margin: 10px 0;
}

.detail-level-slider input {
  flex-grow: 1;
  margin: 0 10px;
}

.detail-level-slider .fa-list,
.detail-level-slider .fa-book {
  color: #4a4a4a;
}

.justified-content {
  text-align: justify;
}

:deep(img) {
  max-width: 100%;
  height: auto;
}
</style>
