<template>
  <div class="item-card" :class="currentView">
    <template v-if="currentView === 'compact'">
      <div class="item-content">
        <span class="item-title">{{ book.displayName || book.title }}</span>
        <span class="item-author">{{ book.author }}</span>
        <span v-if="book.progress !== undefined" class="item-progress"> Read: {{ book.progress }}%</span>
      </div>
    </template>
    
    <template v-else-if="currentView === 'list'">
      <div class="item-image-container">
        <img v-if="book.image" :src="book.image" :alt="book.displayName + ' cover'" class="item-image">
        <div v-else class="item-image-placeholder">
          <font-awesome-icon :icon="['fas', 'book']" />
        </div>
      </div>
      <div class="item-details">
        <div class="item-title-container">
          <span class="item-title">{{ book.displayName || book.title }}</span>
        </div>
        <div class="item-subtitle">{{ book.author }}</div>
        <div v-if="book.artist" class="item-artist">{{ book.artist }}</div>
        <div v-if="book.dateRead" class="completion-date">
          Completed on: {{ formatDate(book.dateRead) }}
        </div>
        <div v-if="book.progress !== undefined" class="progress-container">
Progress:
          <div class="progress-bar">
            <div class="progress" :style="progressStyle"></div>
          </div>
          <span class="progress-text">{{ book.progress }}%</span>
        </div>
      </div>
    </template>

    <template v-else-if="currentView === 'grid'">
      <div class="grid-item-container">
        <img v-if="book.image" :src="book.image" :alt="book.displayName + ' cover'" class="grid-item-image">
        <div v-else class="grid-item-image-placeholder">
          <font-awesome-icon :icon="['fas', 'book']" />
        </div>
        <div class="grid-item-overlay">
          <p class="grid-item-title">{{ book.displayName || book.title }}</p>
          <p class="grid-item-author">{{ book.author }}</p>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { ref, computed, onMounted, watch } from 'vue';

export default {
  name: 'BookItem',
  props: {
    book: {
      type: Object,
      required: true
    },
    currentView: {
      type: String,
      default: 'list'
    }
  },
  setup(props) {
    const animationTriggered = ref(false);
    
    const progressStyle = computed(() => ({
      width: animationTriggered.value ? `${props.book.progress}%` : '0%',
      transition: 'width 1s ease-out'
    }));

    const triggerAnimation = () => {
      animationTriggered.value = false;
      setTimeout(() => {
        animationTriggered.value = true;
      }, 50);
    };

    onMounted(triggerAnimation);

    watch(() => props.currentView, triggerAnimation);

    const formatDate = (dateString) => {
      if (!dateString) return 'Not available';
      const options = { year: 'numeric', month: 'long', day: 'numeric' };
      return new Date(dateString).toLocaleDateString(undefined, options);
    };

    return {
      progressStyle,
      formatDate
    };
  }
}
</script>

