  color: var(--highlight-color);
  transition: color 0.3s ease;
  font-size: 0.8em;
  margin-left: var(--spacing-x-small);
}

.read-online-link:hover {
  color: var(--text-color);
}

.item-artist {
  font-size: var(--font-size-small);
  color: var(--text-color-secondary);
  margin-bottom: var(--spacing-small);
}

.completion-date {
  font-size: var(--font-size-small);
  color: var(--highlight-color);
  margin: var(--spacing-small) 0;
}

/* Additional compact view styles */
.compact-view .item-progress {
  margin-left: auto;
  padding-left: var(--spacing-small);
}

/* Additional list view styles */
.list-view .item-image-container {
  width: 100px;
  height: 150px;
  margin-right: var(--spacing-medium);
  flex-shrink: 0;
}

.list-view .item-details {
  flex-grow: 1;
}

/* Additional grid view styles */
.grid-view .grid-item-container {
  position: relative;
  padding-top: 150%; /* 2:3 aspect ratio */
}

.grid-view .grid-item-image,
.grid-view .grid-item-image-placeholder {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.grid-view .grid-item-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.7);
  padding: var(--spacing-small);
}

.grid-view .grid-item-title {
  color: white;
  margin: 0;
  font-size: var(--font-size-small);
  font-weight: bold;
}

.grid-view .grid-item-author {
  color: var(--text-color-secondary);
  margin: var(--spacing-x-small) 0 0;
  font-size: var(--font-size-x-small);
}
</style>
