// src/store/modules/activities.js
import physicalActivities from '@/assets/physical.json'

export default {
  namespaced: true,
  state: {
    activities: []
  },
  mutations: {
    SET_ACTIVITIES(state, activities) {
      state.activities = activities
    }
  },
  actions: {
    loadActivities({ commit }) {
      commit('SET_ACTIVITIES', physicalActivities)
    }
  },
  getters: {
    getActivities: state => state.activities
  }
}
