// src/store/modules/content.js
import booksData from '@/assets/books.json'
import blogData from '@/assets/blog.json'

export default {
  namespaced: true,
  state: () => ({
    sidebarOpen: false,
    books: [],
    blogPosts: [],
    tableOfContents: [],
    currentBlogPostTitle: ''
  }),
  mutations: {
    toggleSidebar(state) {
      state.sidebarOpen = !state.sidebarOpen
    },
    setSidebarOpen(state, value) {
      state.sidebarOpen = value
    },
    setBooks(state, books) {
      console.log('Setting books in store:', books)
      state.books = books
    },
    setBlogPosts(state, posts) {
      state.blogPosts = posts
    },
    setTableOfContents(state, toc) {
      state.tableOfContents = toc
    },
    setCurrentBlogPostTitle(state, title) {
      state.currentBlogPostTitle = title
    }
  },
  actions: {
    toggleSidebar({ commit }) {
      commit('toggleSidebar')
    },
    setSidebarOpen({ commit }, value) {
      commit('setSidebarOpen', value)
    },
    loadBooks({ commit }) {
      console.log('Loading books from JSON:', booksData)
      commit('setBooks', booksData)
    },
    async loadBlogPosts({ commit }) {
      try {
        const posts = await Promise.all(blogData.posts.map(async (post) => {
          try {
            const [content1, content2, fullContent] = await Promise.all([
              import(`@/assets/blog/${post.slug}/${post.slug}_1.md`),
              import(`@/assets/blog/${post.slug}/${post.slug}_2.md`),
              import(`@/assets/blog/${post.slug}/${post.slug}.md`)
            ])
            
            return {
              ...post,
              contentLevels: [
                content1.default,
                content2.default,
                fullContent.default
              ]
            }
          } catch (error) {
            console.error(`Error loading blog post content for ${post.slug}:`, error)
            return null
          }
        }))
        const validPosts = posts.filter(post => post !== null)
        commit('setBlogPosts', validPosts)
      } catch (error) {
        console.error('Error in loadBlogPosts:', error)
        throw error
      }
    },
    setTableOfContents({ commit }, toc) {
      commit('setTableOfContents', toc)
    },
    setCurrentBlogPostTitle({ commit }, title) {
      commit('setCurrentBlogPostTitle', title)
    }
  },
  getters: {
    isSidebarOpen: state => state.sidebarOpen,
    getBooks: state => {
      console.log('Getting books from store:', state.books)
      return state.books
    },
    getBlogPosts: state => state.blogPosts,
    getBlogPost: (state) => (slug) => {
      return state.blogPosts.find(post => post.slug === slug)
    },
    getTableOfContents: state => state.tableOfContents,
    getCurrentBlogPostTitle: state => state.currentBlogPostTitle
  }
}
